import React, { Component } from 'react';
import PropTypes from 'prop-types';
import './AnswerResult.css';
import { MainImage } from './../MainImage';
import { Button } from './../Button';


export class AnswerResult extends Component {

  render() {
    const answer = this.props.answer;
    const answerResult = answer.result;
    return <div className="block AnswerResult">
      <MainImage src={answerResult.img}/>

      <div className="indent_vert AnswerResult__text">
        { answerResult.text }
      </div>

      <div className="indent_vert">
        <Button text="Продолжить" onPress={this.props.onNextQuestion}/>
      </div>
    </div>;
  }

}


AnswerResult.propTypes = {
  answer: PropTypes.object,
  onNextQuestion: PropTypes.func
};



