// dev only
const staticResults = [{
  id: 1,
  firstName: 'Лена',
  lastName: 'Васечкина',
  score: 2,
  phone: '+79876543210'
}, {
  id: 2,
  firstName: 'Вася',
  lastName: 'Пупкин',
  score: 4,
  phone: '+79212345678'
}];


const resultsStorageKey = 'results-mock';


export function getMockResults() {
  const rawStoredResults = localStorage.getItem(resultsStorageKey);
  const results = rawStoredResults ? JSON.parse(rawStoredResults) : staticResults;
  return results;
}


export function saveMockResults(results) {
  const rawStoredResults = JSON.stringify(results);
  localStorage.setItem(resultsStorageKey, rawStoredResults);
}


export function clearMockResults() {
  localStorage.removeItem(resultsStorageKey);
}



