import React, { Component } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { actions } from '../../redux';
import PropTypes from 'prop-types';
import './Results.css';
import { Button } from './../Button';
import { clearMockResults } from './../../api/results-mock';

const introText = 'Lorem ipsum dolor sit amet, consectetur adipiscing elit, '
  + 'sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.';

class _Results extends Component {

  renderResults(results) {
    if (!results || !results.length) {
      return null;
    }

    const topPlayers = this.getTopResults(results);

    return <table className="Results__table">
      <thead>
        <tr>
          <td colSpan="2" className="Results__table-head-title">
            Топ игроков
          </td>
          <td className="Results__table-head-scores">
            Очки
          </td>
        </tr>
        <tr>
          <td colSpan="3">
            <div className="Results__table-head-delimiter"></div>
          </td>
        </tr>
      </thead>
      <tbody>
        { topPlayers.map((player, index) => <tr key={player.id}>
            <td className="Results__table-result-index">
              { index + 1}
            </td>
            <td className="Results__table-result-name">
              { player.firstName } { player.lastName}
            </td>
            <td className="Results__table-result-score">
              { player.score }
            </td>
          </tr>)
        }
      </tbody>
    </table>;
  }


  renderActions() {
    return <React.Fragment>
      <Button text="Играть" onPress={this.startGame.bind(this)}/>
      { this.props.isDev && <Button text="Clear" onPress={this.clearMockResultsAndRestart.bind(this)}/> }
    </React.Fragment>;
  }


  renderIntro() {
    return <div className="Results__intro">{ introText }</div>;
  }


  render() {
    return <div className="block Results">
      { this.renderIntro() }
      <div className="indent_vert">
        { this.renderActions() }
      </div>
      <div className="indent_vert">
        { this.renderResults(this.props.results) }
      </div>
    </div>;
  }


  clearMockResultsAndRestart() {
    clearMockResults();
    window.location.reload();
  }


  startGame() {
    this.props.actions.registerInGame();
  }


  getTopResults(results) {
    const topResults = results.slice().sort((resultA, resultB) => resultB.score - resultA.score);
    return topResults;
  }

}


_Results.propTypes = {
  actions: PropTypes.object,
  isDev: PropTypes.bool,
  waiting: PropTypes.bool,
  results: PropTypes.array
};


function mapStateToProp(state) {
  return {
    isDev: state.isDev,
    waiting: state.waitingForResults,
    results: state.results
  };
}


function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators(actions, dispatch)
  };
}


export const Results = connect(mapStateToProp, mapDispatchToProps)(_Results);



