import { questions } from './questions-mock';
import { getMockResults, saveMockResults } from './results-mock';
import { getUserResults } from './user-results-mock';


function delay(timeout) {
  return new Promise(resolve => setTimeout(resolve, timeout || 0));
}


const cache = {
  questions: null,
  questionsNumber: 0
};


export const API = {

  _results: getMockResults(),

  fetchResults() {
    return delay().then(() => API._results.slice());
  },


  fetchQuestion(userInfo, prevQuestion) {
    return delay()
      .then(() => API.fetchQuestions())
      .then((questions) => {
        const currentNum = prevQuestion ? prevQuestion.num : 0;
        const nextNum = currentNum + 1;
        const question = questions.filter(q => q.num === nextNum)[0] || null;
        return question;
      });
  },


  fetchQuestions() {
    if (cache.questions) {
      return Promise.resolve(cache.questions);
    }
    return delay().then(() => {
      cache.questions = questions;
      cache.questionsNumber = questions.length;
      return questions;
    });
  },


  applyAnswer(userInfo, question, answer) {
    return delay().then(() => {
      const updatedUserInfo = Object.assign({}, userInfo);
      updatedUserInfo.aValue += answer.aValue;
      updatedUserInfo.bValue += answer.bValue;
      return updatedUserInfo;
    });
  },


  fetchUserResult(userInfo) {
    return delay().then(() => {
      const updatedUserInfo = Object.assign({}, userInfo);
      updatedUserInfo.score = userInfo.aValue + userInfo.bValue;
      this._addToResults(updatedUserInfo);

      const userResult = getUserResults(updatedUserInfo);
      return {
        userInfo: updatedUserInfo,
        userResult
      };
    });
  },


  _addToResults(userInfo) {
    const results = API._results;
    const existingUserInfo = results.filter(user => user.firstName === userInfo.firstName
      && user.lastName === userInfo.lastName)[0];
    if (existingUserInfo) {
      existingUserInfo.aValue = userInfo.aValue;
      existingUserInfo.bValue = userInfo.bValue;
      existingUserInfo.score = userInfo.score;
      return;
    }

    const maxId = results.reduce((id, user) => id < user.id ? user.id : id, 0);
    const userInfoToSave = Object.assign({}, userInfo, { id: maxId + 1 });
    results.push(userInfoToSave);
    saveMockResults(results);
  },


  getQuestionsNumber() {
    return cache.questionsNumber;
  },


  generateUserInfo(firstName, lastName, phone) {
    return {
      id: 0,
      firstName,
      lastName,
      phone,
      score: 0,
      aValue: 0,
      bValue: 0
    };
  }

};



